<template>
  <div class="employee-card">
    <img class="employee-thumb" :src="employee.image"/>
    <div class="info-panel">
      <h3>{{ employee.name }}</h3>
      <div class="stat-bars">
        <p>Salary</p>
        <div class="option-buttons">
          <p style="margin-right: 1ch;">$ {{ (employee.salary.value * 60).toFixed(0) }} / day </p>
          <TextButton @click="employee.salary.baseValue -= 0.1"> <p>-</p> </TextButton>
          <TextButton @click="employee.salary.baseValue += 0.1"> <p>+</p> </TextButton>
        </div>

        <p>Assignment</p>
        <div class="option-buttons">
          <TextButton @click="employee.assignment = 'admin'" :class="{'selected-ass': employee.assignment === 'admin'}"> <p>Admin</p> </TextButton>
          <TextButton @click="employee.assignment = 'farm'" :class="{'selected-ass': employee.assignment === 'farm'}"> <p>Farm</p> </TextButton>
          <TextButton @click="employee.assignment = 'lab'" :class="{'selected-ass': employee.assignment === 'lab'}"> <p>Lab</p> </TextButton>
        </div>

        <p>Intelligence</p>
        <ValueMeter :container="employee.iq" barColor="#149">
          <p>{{ employee.iq.value.toFixed(0) }} IQ</p>
        </ValueMeter>

        <p>Happiness</p>
        <ValueMeter :container="employee.mood" barColor="#194">
          <p>{{ employee.mood.text }} ({{ deltaSymbol(employee.moodDelta.value) }})</p>
        </ValueMeter>

        <p>Milk</p>
        <div class="option-buttons" style="grid-template-columns: 2fr 1fr">
          <ValueMeter :container="employee.milk" barColor="#abc">
            {{ employee.milk.value.toFixed(2) }} L (+ {{(employee.milkDelta.value * 60).toFixed(2)}} L/day)
          </ValueMeter>
          <TextButton @click="farm.milk(employee)"> <p>Milk</p> </TextButton>
        </div>
      </div>
      <div class="equipment-list">
        <template v-for="research in farm.state.lab.availableForEmployee(employee)">
          <img class="research-thumb"
               :src="research.image"
               @click="employee.toggleEquip(research)"
               :class="{ equipped: !employee.canEquip(research) }"
               :title="research.name"
          />
        </template>
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import { Options, Vue } from 'vue-class-component'
import Employee from '@/Employee'
import Farm from '@/Farm'
import ValueMeter from '@/components/ValueMeter.vue'
import TextButton from '@/components/TextButton.vue'

@Options({
  components: {
    ValueMeter, TextButton
  },
  props: {
    employee: Employee, farm: Farm
  },
  methods: {
    log(msg: any) {
      console.log(msg)
    },
    deltaSymbol(value: number): string {
      if (value > 0) return 'Increasing'
      else if (value < 0) return 'Decreasing'
      return 'Stable'
    }
  }
})
export default class EmployeeCard extends Vue {
  employee!: Employee
  farm!: Farm
}
</script>

