import CollectionPlayer from "@/CollectionPlayer";
import util from "@/util";
import {list, map, object, primitive, serializable} from "serializr";
import ValueMod from "@/ValueMod";

export default class ValueRange {
  @serializable
  private _baseValue: number = 0
  @serializable
  private _min: number = 0
  @serializable
  private _max: number = 0

  @serializable(map(object(ValueMod)))
  public valueMods: { [index: string]: ValueMod } = {}

  @serializable(map(object(ValueMod)))
  public minMods: { [index: string]: ValueMod } = {}

  @serializable(map(object(ValueMod)))
  public maxMods: { [index: string]: ValueMod } = {}

  @serializable(list(primitive()))
  private readonly texts: string[] = []

  constructor(value: number, min: number, max: number | undefined = undefined, text: string[] = []) {
    this._baseValue = value
    this.min = min
    this.max = (max == undefined) ? Number.MAX_SAFE_INTEGER : max
    this.texts = text
  }

  set fraction(fraction: number) {
    this.baseValue = this.min + this.max * fraction
  }

  get fraction(): number {
    return (this.value - this.min) / (this.max - this.min)
  }

  get text(): string {
    if (this.texts.length == 0) return this.value.toString()
    return this.texts[Math.min(Math.round(this.fraction * this.texts.length), this.texts.length - 1)]
  }

  get value(): number {
    let sum = ValueMod.activateAll(this._baseValue, Object.values(this.valueMods))

    if (sum > this.max) {
      return this.max
    } else if (sum < this.min) {
      return this.min
    } else {
      return sum
    }
  }

  set baseValue(value: number) {
    this._baseValue = value
  }

  get baseValue(): number {
    return this._baseValue
  }

  get min(): number {
    return ValueMod.activateAll(this._min, Object.values(this.minMods));
  }

  set min(value: number) {
    this._min = value;
  }

  get max(): number {
    return ValueMod.activateAll(this._max, Object.values(this.maxMods));
  }

  set max(value: number) {
    this._max = value;
  }

  load(state: ValueRange) {
    this.min = state.min
    this.max = state.max
    this.baseValue = state.baseValue
  }

  get copy(): ValueRange {
    let v = new ValueRange(this._baseValue, this._min, this._max, this.texts)
    v.valueMods = this.valueMods
    return v
  }
}
