import Research from "./Research";
import util from "@/util";
import globalState from "@/store/globalState";
import { unref } from 'vue';
import Employee from "@/Employee";
import {list, object, serializable} from "serializr";
import ValueRange from "@/ValueRange";
import ValueMod from "@/ValueMod";

export default class Laboratory {

  @serializable(list(object(Research)))
  private allNodes: Research[] = []
  @serializable(object(Research))
  private _currentResearch: Research | undefined = undefined

  public researchers: Employee[] = []

  constructor(treedata: any = { nodes: [] }) {
    for (let node of treedata.nodes) {
      this.allNodes.push(new Research(node))
    }
  }

  update(dt: number, assigned: Employee[]) {
    this.researchers = assigned

    if (this._currentResearch) {
      this._currentResearch.research(this.labPower * dt)
      if (this._currentResearch.completion.fraction >= 1) this._currentResearch = undefined
    }
  }

  startResearch(research: Research) {
    if (this.available.includes(research)) {
      // If we are already researching something, mark that research as paused
      if (this._currentResearch) this._currentResearch.isBeingResearched = false
      // Start the new research
      this._currentResearch = research
      this._currentResearch.isBeingResearched = true
    }
  }

  getResearchCost(research: Research): number {
    return research.cost.value
  }

  get labPower(): number {
    let sum = 0
    for (let r of this.researchers) {
      let uselessCutoff = 0.3
      let contribution = (r.iq.fraction - uselessCutoff) * 2.3
      //if (r.iqStat.fraction < uselessCutoff) contribution = -contribution
      sum += contribution
    }
    return sum * 10
  }

  get completed(): Research[] {
    let completed: Research[] = []
    for (let node of this.allNodes) {
      if (node.isCompleted) {
        completed.push(node)
      }
    }
    return completed
  }

  get available(): Research[] {
    let completed = this.completed
    let available = []
    // Check all nodes
    for (let node of this.allNodes) {
      // If a node is not completed, it might be available
      if (!node.isCompleted) {
        // But only if all of its requirements ARE completed.
        let i = 0
        // Count how many requirements are fulfilled
        for (let requirement of node.requires) {
          if (completed.find(research => research.name == requirement)) {
            i += 1
          }
        }
        // Check if all are completed
        if (i == node.requires.length) available.push(node)
      }
    }

    return available
  }

  get currentResearch(): Research | undefined {
    return this._currentResearch
  }

  get currentResearchProgress(): ValueRange {
    if (this._currentResearch == undefined) {
      return new ValueRange(0, 0, 1)
    }
    return this._currentResearch.completion
  }

  availableForEmployee(employee: Employee): Research[] {
    let available: Research[] = []
    for (let research of this.completed) {
      if (employee.canEquip(research) || employee.isEquipped(research)) {
        available.push(research)
      }
    }
    return available
  }

  researchByName(name: string) {
    return this.allNodes.find(research => research.name === name)
  }
}
