import Employee from "@/Employee";
import util from "@/util";
import ValueRange from "@/ValueRange";
import { reactive, toRaw } from "vue";
import Laboratory from "@/Laboratory";
import globalState from "@/store/globalState";
import Research from "./Research";
import {deserialize, serialize} from "serializr";
import CollectionPlayer from "@/CollectionPlayer";
import ValueMod from "@/ValueMod";

export default class Farm {
  state = reactive({
    employees: [] as Employee[],
    milk: new ValueRange(0, 0, 10),
    milkSold: 0,
    money: 10000,
    time: 0,
    lab: new Laboratory(),
    researchSpending: 0
  })

  private lastUpdate: number = 0

  constructor(researchTree: Laboratory) {
    this.state.lab = researchTree
    this.startUpdate()
  }

  save() {
    let saveObject = toRaw(this.state)
    console.log(saveObject)
    localStorage.setItem("farm-state", JSON.stringify(saveObject))
  }

  load(state: any) {
    this.state.lab = deserialize(Laboratory, state.lab)

    this.state.employees = []
    for (let employee of state.employees) {
      // @ts-ignore
      let d: Employee = deserialize(Employee, employee)
      this.state.employees.push(d)
    }

    this.state.milkSold = state.milkSold
    this.state.money = state.money
    // @ts-ignore
    let milk: ValueRange = deserialize(ValueRange, state.milk)
    this.state.milk.load(milk)
    this.state.researchSpending = state.researchSpending
  }

  loadFromLocalStorage() {
    let stateString = localStorage.getItem("farm-state")
    if (stateString == null) return
    let state = JSON.parse(stateString)
    this.load(state)
  }

  startUpdate() {
    this.lastUpdate = performance.now()
    console.log("Starting update")
    setInterval(() => this.update(), 100)
  }

  update() {
    let dt = (performance.now() - this.lastUpdate) / 1000
    this.lastUpdate = performance.now()

    // For now only pay employees while milk is produced
    if (this.state.milk.fraction < 1) {
      //this.state.milk.baseValue += this.production * dt
      for (let employee of this.state.employees) {
        let salary = employee.salary.value * dt
        employee.pay(salary)
        this.state.money -= salary

        if (salary < 0.5 * employee.targetSalary * dt) {
          employee.moodDelta.valueMods.salary = new ValueMod(-0.05, 'add')
        } else if (salary < employee.targetSalary * dt) {
          employee.moodDelta.valueMods.salary = new ValueMod(-0.01, 'add')
        } else if (salary > 1.4 * employee.targetSalary * dt) {
          employee.moodDelta.valueMods.salary = new ValueMod(0.01, 'add')
        } else {
          employee.moodDelta.valueMods.salary = new ValueMod(0, 'add')
        }
      }
    }

    this.state.employees.map(e => e.update(dt))
    this.state.lab.update(dt, this.labWorkers)
    this.state.time += dt
  }

  get milkprice(): number {
    return 30
  }

  sell() {
    let cost = this.state.milk.value * this.milkprice
    this.state.milk.baseValue = 0
    this.state.money += cost
    this.state.milkSold += cost
  }

  research(research: Research) {
    let cost = this.state.lab.getResearchCost(research)
    if (this.state.money > cost || research.isStarted) {
      if (!research.isStarted) {
        this.state.money -= cost
        this.state.researchSpending += cost
      }
      this.state.lab.startResearch(research)
    }
  }

  milk(employee: Employee) {
    this.state.milk.baseValue += employee.milk.value
    employee.milk.baseValue = 0
  }

  setAssignment(e: Employee, assignment: string) {
    e.assignment = assignment
  }

  get labWorkers(): Employee[] {
    let workers: Employee[] = []
    for (let e of this.state.employees) {
      if (e.assignment === 'lab') {
        workers.push(<Employee> e)
      }
    }
    return workers
  }

  get currentSalaries(): number {
    let sum = this.state.employees.map((e) => e.salary.value).reduce((acc, val) => acc + val, 0)
    return sum
  }

  get lifetimeSalaries(): number {
    let sum = this.state.employees.map((e) => e.money).reduce((acc, val) => acc + val, 0)
    return sum
  }

  get production(): number {
    let sum = this.state.employees.map((e) => e.milkDelta.value).reduce((acc, val) => acc + val, 0)
    return sum
  }

  get productionValue(): number {
    return this.production * this.milkprice
  }

  get researchSpending(): number {
    return this.state.researchSpending
  }

  hiringCost(): number {
    return 400 * Math.pow(this.state.employees.length, 2)
  }

  async hire() {
    if (this.state.money < this.hiringCost()) return
    this.state.money -= this.hiringCost()

    let info = await util.getInfo("liliana")
    let firstnames = require("@/assets/names.json").firstnames
    this.state.employees.push(new Employee(util.sample(firstnames), info))
  }

  employeeById(uuid: string) {
    for (let e of this.state.employees) {
      if (e.uuid === uuid) return e
    }
    return undefined
  }
}
